// 对城市信息进行分组
function cityList() {
  let tempArr = [];

  searchLetter.map(
    initial => {
      let tempObj = {};
      // let cityInfo = [];

      tempObj.initial = initial;
      tempObj.cityInfo = cityObj.filter(
        city => city.initial === initial
      );

      tempArr.push(tempObj);
    }
  );

  // console.log(JSON.stringify(tempArr));
  return tempArr;
}

